import evanSwitch from '../../../components/evan-switch/evan-switch.vue'
import uniPopup from "../../../components/uni-popup/uni-popup.vue"
import {BLACKLIST,BLACKADD,BLACKDELETE,REFUSECALL,REFUSECALLSET} from '../../../utils/api.js'
export default {
	components:{evanSwitch,uniPopup},
	data() {
		return {
			zhapian: '',
			dingding: '',
			pageSize: 0,
			pageNum: 10,
			deleteMobile: '',
			listData:[],
			RefuseCall: '',//拒接状态
		}
	},
	onLoad() {
		uni.showToast({
			title:'加载中...',
			icon:'none'
		})
		//获取黑名单列表
		this.getBlackList()
		// 查询拒接状态
		this.getRefuseCall()
	},
	methods: {
		// 查询拒接状态
		getRefuseCall(){
			const params = {}
			this.$request.url_request(REFUSECALL,params,'GET',res=>{
				this.RefuseCall = JSON.parse(res.data).data.data
				console.log(this.RefuseCall)
				this.zhapian = this.RefuseCall.defraud ==1?true: false
				this.dingding = this.RefuseCall.dingding ==1?true: false
			},err=>{})
		},
		//获取黑名单列表
		getBlackList(){
			const params ={
				pageSize: this.pageSize,
				pageNum: this.pageNum,
			}
			this.$request.url_request(BLACKLIST,params,'GET',res=>{
				uni.hideToast()
				this.listData = JSON.parse(res.data).data.data.list
			},err=>{})
		},
		// 取消删除黑名单
		deleteBlackCacel(){
			this.$refs.deleteBlack.close()
		},
		// 删除黑名单
		deleteBlack(item){
			this.$refs.deleteBlack.open()
			this.deleteMobile = item.mobile
		},
		// 确认删除黑名单
		deleteBlackSure(){
			var that = this
			const params = {
				mobile: this.deleteMobile
			}
			this.$request.url_request(BLACKDELETE,params,'GET',res=>{
				console.log(JSON.parse(res.data).data.code)
				if(JSON.parse(res.data).data.code == 200){
					that.$refs.deleteBlack.close()
					that.getBlackList()
				} else{
					uni.showToast({
						title:JSON.parse(res.data).data.resultMsg,
						icon:'none',
						duration:1200
					})
				}
			},err=>{})
		},
		// 添加黑名单
		addBlack(){
			uni.navigateTo({
				url:'../addBlack/addBlack'
			})
		},
		// 状态更改
		statusChange(status,num){
			var that = this
			console.log(num)
			num = num == 1?0:1
			const params = {
				type: status,
				status:num
			}
			console.log(params)
			this.$request.url_request(REFUSECALLSET,params,'GET',res=>{
				that.getRefuseCall()
			},err=>{})
		}
	}
}
